module.exports = "<h5 class=\"book-header text-center\">寻找同学们分享的资料</h5><div class=\"list-view col-xs-12 col-sm-offset-1 col-sm-10 col-md-offset-2 col-md-8\"><div class=\"book-search-wrapper input-group col-xs-12 col-sm-8 col-sm-offset-2\"><input class=form-control placeholder=\"试一试搜索: 线性代数\" v-model=searchKey v-on=\"keyup: onSearchBook | key 'enter'\"> <span class=input-group-btn><button class=\"btn btn-default\" type=button v-on=\"click: onSearchBook\">搜索</button></span></div><div class=text-center v-show=\"tips!=''\"><small>{{tips}}</small></div><div class=book-list><div class=bk-wrapper v-repeat=book:bookList v-on=\"click: showBookDetail(book)\"><div class=bk-name><strong>{{book.name}}</strong></div><div class=bk-detail><small>{{book.detail}}</small></div></div></div><div class=row><div class=text-center v-show=moreData v-on=\"click: loadMore\" style=cursor:pointer>加载更多</div></div></div><modal show={{@showDetailModal}} width={{modalWidth}} default-title={{modalTitle}}><div class=modal-body><p><strong>文件名:</strong> {{bookInfo.name}}</p><p><strong>详细说明:</strong> {{bookInfo.detail}}</p><p><strong>贡献者:</strong> {{bookInfo.user}}</p><p><strong>分享时间:</strong> {{bookInfo.time}}</p></div><div class=modal-footer><button class=\"btn btn-primary btn-wide btn-warning\" v-on=\"click: showDetailModal = false\">取消</button> <button class=\"btn btn-primary btn-wide\" v-on=\"click: onBuy\">预定</button></div></modal>";


