module.exports = {
  props: {
    show: {
      type: Boolean,
      required: true,
      twoWay: true,
    },
    stage: String,
    verifyWay: String,
    verifyType: String,
    verifyInfo: String,
  },

  data: function () {
    return {
      modalWidth: 600,
      code: "",
      password: "",
      errorInfo: "",
    }
  },

  computed: {
    modalTitle: function() {
      if(this.stage=='code') {
        return "输入验证码"
      } else if(this.stage=='reset') {
