    codeInfo: function() {
      if(this.verifyWay=='phone') {
        return "验证码已经发送到: "+ this.verifyInfo + "，请输入验证码完成操作"
      } else if(this.verifyWay=="email") {
        return "验证码已经发送到: "+ this.verifyInfo + "，请输入验证码或点击邮件中的验证链接完成操作"
      }
