    method: 'post',
    api: 'password/code',
    data: {
      code: vuemodel.code,
    },
    opSuccess: function(info) {
      vuemodel.stage = 'reset'
      vuemodel.errorInfo = ""
    },
    opFail: function(info) {
      vuemodel.errorInfo = info
    },
  })
}

function resetPassword(vuemodel) {
  var md5 = require("blueimp-md5")
  yy_request.rest_api({
    method: 'post',
    api: 'password/',
    data: {
      password: md5(vuemodel.password)
    },
    opSuccess: function(info) {
      vuemodel.stage = 'finish'
      vuemodel.errorInfo = ""
    },
    opFail: function(info) {
      vuemodel.errorInfo = info
    },
  })
}


